					}
				],
			}
		},
		onLoad() {
			this.getBanner()
		},
		methods: {
			getBanner() {
				// 正在加载状态
				this.loading = true
				uni.request({
					// url: 'http://localhost:3000/banner',
					url: 'https://www.gzamon.wang/api/banner',
					method: 'GET',
					data: {},
					success: res => {
						this.swiper = res.data.banners
					},
					complete: () => {
						// 加载完成状态
						this.loading = false
					}
				});
			},
			switchTab (type) {
			    this.newType = type
			    // 根据类型加载不同数据
			    if (type == 1) {
			        // 新碟数据
			    }
			    if (type == 2) {
			        // 新歌数据
			    }
			},
		}
	}
</script>

