	.main-bar {
		padding-bottom: 22rpx;
		text-align: center;
		line-height: 70rpx;
		color: #666;
		border-bottom: 1rpx solid #e6e6e6;
		.img {
			display: block;
			width: 70rpx;
			height: 70rpx;
			margin: 0 auto;
		}
	}
	.banner{
		margin-top: 60rpx;
